
.si_ting{
      width: 50px;
      display: inline-block;
      float: right;
}
.si_item{
      width: 90%;
      padding: 0px;
      border-bottom: 1px solid #8B8B7A;
      margin-top: 10px;
      overflow: hidden;
}
.si_item img{
      width: 20px;
      height: 20px;
}
.not{
      width: 300px;
      height: 200px;
}
.not span{
      width: 100%;
      display: inline-block;
      line-height: 200px;
      text-align: center;
}



