
.moha span {
    line-height: 40px;
    font-size: 14px;
    /*background: #2A6EB9;
    background: -webkit-linear-gradient(left, #2A6EB9 0%, #113160 100%);
    color: #0063B1;*/
    background: -webkit-linear-gradient(left, #2A6EB9 0%, #113160 100%);
    -webkit-background-clip: text;
    color: transparent;
    text-transform: uppercase;
}
.moha {
    width: 95%;
    height: 40px;
    margin-top: 10px;
    margin-left: 8px;
    background-color: #FFF;
    border: 1px solid #7c6e74;
    border-radius: 5px;
}
.tou_list {
    width: 100%;
    padding: 0px;
    margin: 0px;
    overflow: hidden;
}
.ctt {
    float: right;
}
.tou_li {
    width: 95%;
    height: 36px;
    margin-left: 68px;
    border-bottom: 1px solid #E0E0E0;
}
@media screen and (max-width: 480px) {
.tou_li {
        width: 95%;
        height: 36px;
        margin-left: 19px;
        border-bottom: 1px solid #E0E0E0;
}
}
.tou_li span {
    width: auto;
    height: inherit;
    display: inline-block;
    vertical-align: middle;
}
/*
    display:inline-block,变成同行块级元素
*/
.tou_li div {
    width: 10%;
    height: inherit;
    margin: 0px;
    padding: 0px;
    display: inline-block;
}
.tou_li img {
    width: 25px;
    height: 25px;
    margin-top: 5px;
    margin-left: 5px;
}



