
.woc {
    width: 100%;
    height: auto;
}
.woc img {
    width: inherit;
    height: 200px;
}
.nv_list {
    margin-top: 10px;
    padding: 0px;
    line-height: 100px;
}
.nv_item {
    width: 30%;
    line-height: 30px;
    text-align: center;
    font-size: 10px;
    font-family: "SimHei";
    display: inline-block;
}
.nv_item img {
    width: 50px;
    height: 50px;
}
.pi p {
    margin-bottom: 0px;
    width: 100%;
    text-align: center;
    background-color: #E0E0E0;
    font-size: 5px;
    position: fixed;
    z-index: 1000;
    bottom: 0px;
}



