
.twelve_kuai {
    width: 90%;
    height: auto;
    border: 1px solid #CCC;
    border-radius: 5px;
    margin-top: 10px;
    margin-left: 5%;
}
.twelve_list {
    margin: 0px;
    padding: 0px;
    line-height:25px;
}
.haha{
    border-bottom: 1px solid #CCC;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack:justify;
        -ms-flex-pack:justify;
            justify-content:space-between;
}
.haha span{
    color:#00f;
}
.haha font{
    color:#f00;
}
.twelve_list li:nth-of-type(even){
    background-color:#CCC;
}



